<template>
  <Table stripe border :columns="columns" :data="data" :style="table">
    <template slot-scope="{ row, index }" slot="code">
      <Input type="text" v-model="editCode" v-if="editIndex === index" />
      <span v-else>{{ row.code }}</span>
    </template>

    <template slot-scope="{ row, index }" slot="name">
      <Input type="text" v-model="editName" v-if="editIndex === index" />
      <span v-else>{{ row.name }}</span>
    </template>

    <template slot-scope="{ row, index }" slot="action">
      <div v-if="editIndex === index">
        <Button :style="margin" @click="handleSave(index)">保存</Button>
        <Button @click="editIndex = -1">取消</Button>
      </div>
      <div v-else>
        <Button @click="handleEdit(row, index)">操作</Button>
      </div>
    </template>

    <template slot-scope="{ row, index }" slot="check">
      <Checkbox @click.native="show(index, $event)"></Checkbox>
    </template>
  </Table>
</template>
<script>
export default {
  name: 'TableZ',
  props: ['addValue', 'addSign'],
  data() {
    return {
      columns: [
        {
          title: 'コード',
          slot: 'code',
          width: 100,
          align: 'center'
        },
        {
          title: '表示名称',
          slot: 'name',
          align: 'center'
        },
        {
          title: '操作',
          slot: 'action',
          width: 200,
          align: 'center'
        },
        {
          title: '対象',
          width: 65,
          align: 'center',
          slot: 'check'
        }
      ],
      data: [
        {
          code: '0100',
          name: '開発本部'
        },
        {
          code: '0101',
          name: '開発本部一課'
        },
        {
          code: '0102',
          name: '開発本部二課'
        },
        {
          code: '0200',
          name: '営業本部'
        }
      ],
      editIndex: -1, // 当前聚焦的输入框的行数
      editCode: '', // 第一列输入框，当然聚焦的输入框的输入内容，与 data 分离避免重构的闪烁
      editName: '', // 第二列输入框
      margin: {
        'margin-right': '10px'
      },
      table: {
        'max-width': '60vw',
        left: '50%',
        transform: 'translateX(-50%)'
      }
    }
  },
  methods: {
    handleEdit(row, index) {
      this.editName = row.name
      this.editCode = row.code
      this.editIndex = index
    },
    handleSave(index) {
      this.data[index].name = this.editName
      this.data[index].code = this.editCode
      this.editIndex = -1
    },
    show(index, e) {
      if (e.target.checked) {
        this.$emit('is-check', this.data[index], true)
      }
      if (e.target.checked === false) {
        this.$emit('is-check', this.data[index], false)
      }
    }
  },
  watch: {
    addSign: function() {
      let code = this.addValue.code
      let name = this.addValue.name
      const check = { _checked: true }
      this.data.push({ code, name, check })
      console.log(this.editIndex)
    }
  }
}
</script>ivu-table-cell
<style lang="scss" scoped>
</style>
