<template>
  <div class="home-page">
    <div class="layout">
        <Layout>
            <Header>
                <Menu mode="horizontal" theme="dark" active-name="1" @on-select="handleSelected">
                    <!-- <div class="layout-logo"></div> -->
                    <div class="layout-nav">
                        <MenuItem name="1">
                            <Icon type="ios-navigate" />
                            随時処理
                        </MenuItem>
                        <MenuItem name="2">
                            <Icon type="ios-paper" />
                            申請・承認
                        </MenuItem>
                        <MenuItem name="3">
                            <Icon type="ios-calendar" />
                             月次処理
                        </MenuItem>
                        <MenuItem name="4">
                            <Icon type="ios-cloudy-night" />
                            有休管理
                        </MenuItem>
                        <Submenu name="5">
                            <template slot="title"><Icon type="ios-albums" />マスタ登録</template>
                                <MenuItem name="5-1">社員区分登録</MenuItem>
                                <MenuItem name="5-2">勤務体系登録</MenuItem>
                                <MenuItem name="5-3">所属登録</MenuItem>
                                <MenuItem name="5-4">社員登録・ICカード割当</MenuItem>
                                <MenuItem name="5-5">勤怠管理対象外社員登録</MenuItem>
                                <MenuItem name="5-6">所属長兼任設定</MenuItem>
                                <MenuItem name="5-7">祝日登録</MenuItem>
                                <MenuItem name="5-8">時間休設定</MenuItem>
                                <MenuItem name="5-9">ICカード登録要求一覧</MenuItem>
                                <MenuItem name="5-10">端末登録</MenuItem>
                                <MenuItem name="5-11">環境設定</MenuItem>
                        </Submenu>
                    </div>
                </Menu>
            </Header>
                <Layout :style="warp">
                    <Content :style="content">
                      <div v-if="show">
                        <Divider dashed orientation="left"><h2>{{ choose }}</h2></Divider>
                        <TableZ :addValue="value" :addSign="addSign" @is-check="onIsCheck" ref="tableData"></TableZ>
                        <div class="button-group">
                          <Button type="success" @click="add" icon="md-add">追加</Button>
                          <Button type="primary" icon="ios-cloud">更新</Button>
                          <Button type="error" @click="remove" icon="md-trash">削除</Button>
                        </div>
                      </div>
                        <Divider dashed orientation="left" v-else><h2>{{ choose }}</h2></Divider>
                    </Content>
                </Layout>
            </Layout>
    </div>
  </div>
</template>
<style lang="scss" scoped>
.button-group {
  margin-top: 10px;
  button {
    margin-right: 10px;
  }
}
</style>
<script>
import TableZ from './TableZ.vue'
export default {
  name: 'HomePage',
  components: {
    TableZ
  },
  data: function() {
    return {
      warp: {
        padding: '24px'
      },
      content: {
        padding: '24px',
        minHeight: '85vh',
        background: '#fff',
        border: '1px solid transparent',
        'border-radius': '6px',
        'box-shadow': '0 2px 7px rgba(0,0,0,.15)'
      },
      show: false,
      choose: 'マスタ登録の所属登録を選択してください',
      addSign: false,
      index: [],
      value: {
        code: '',
        name: ''
      }
    }
  },
  methods: {
    handleSelected(name) {
      if (name) {
        if (name === '5-3') {
          this.choose = '所属登録'
          return (this.show = true)
        }
      }
      this.show = false
      return (this.choose = 'この機能がまだ開発中ので、再選択してください。')
    },
    add() {
      this.$Modal.confirm({
        render: h => {
          return h('div', [
            h('Input', {
              props: {
                autofocus: true,
                placeholder: 'コード'
              },
              style: {
                'margin-bottom': '10px'
              },
              on: {
                input: val => {
                  this.value.code = val
                }
              }
            }),
            h('Input', {
              props: {
                autofocus: true,
                placeholder: '所属名称'
              },
              style: {
                'margin-bottom': '10px'
              },
              on: {
                input: val => {
                  this.value.name = val
                }
              }
            })
          ])
        },
        title: '所属登録',
        onOk: () => {
          this.addSign = !this.addSign
          this.$Message.success('追加完了')
        }
      })
    },
    onIsCheck: function(data, isOk) {
      if (isOk) {
        let index = [...this.index, data]
        this.index = [...new Set(index)]
      }
      if (isOk === false) {
        this.index = this.index.filter(e => e !== data)
      }
    },
    remove() {
      this.$Modal.confirm({
        title: '削除してよろしいでしょうか',
        onOk: () => {
          let data = this.index
            .concat(this.$refs.tableData.data)
            .filter(function(v, i, arr) {
              return arr.indexOf(v) === arr.lastIndexOf(v)
            })
          if (data) {
            this.$refs.tableData.data = data
          }
          this.$Message.success('削除完了')
        }
      })
      // this.data.splice(index, 1)
    }
  }
}
</script>
